import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import { styled } from '@mui/material/styles';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import DownloadIcon from '@mui/icons-material/Download';
import Link from '@mui/material/Link';
import Button from '@mui/material/Button'
export default class Header extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            height: 0,
            scrolltop: 0,
            action: {
                home: 0,
                summary: 0,
                about: 0,
                education: 0,
                employement: 0,
                skills: 0
            },
            action_name: ''
        };
        this.handleScroll = this.handleScroll.bind(this)
    }

    componentDidMount() {
        const height = this.divElement.clientHeight;
        const home = document.getElementById("home");
        const summary = document.getElementById("summary");
        const about = document.getElementById("about");
        const education = document.getElementById("education");
        const employement = document.getElementById("employement");
        const skills = document.getElementById("skills");
        this.setState({
            height: height,
            action:
            {
                home: home.clientHeight,
                summary: summary.clientHeight,
                about: about.clientHeight,
                education: education.clientHeight,
                employement: employement.clientHeight,
                skills: skills.clientHeight
            }
        });
        window.addEventListener("scroll", this.handleScroll);
       
    }
   
    componentDidUpdate(prevProps, prevState) {
        const scrollTop2 = window.pageYOffset + 1;
        if (scrollTop2 !== prevState.scrolltop) {
            this.setState({ scrolltop: scrollTop2 });

        }
        document.getElementById("home-link").addEventListener("click", () => {
            window.location.hash = "home"

        })
        document.getElementById("summary-link").addEventListener("click", () => {
            window.location.hash = "summary"
        })
        document.getElementById("about-link").addEventListener("click", () => {
            window.location.hash = "about"
        })
        document.getElementById("education-link").addEventListener("click", () => {
            window.location.hash = "education"
        })
        document.getElementById("employement-link").addEventListener("click", () => {
            window.location.hash = "employement"
        })
        document.getElementById("skills-link").addEventListener("click", () => {
            window.location.hash = "skills"
        })
    }

    handleScroll(event) {
        const scrollTop = window.scrollTo;
        this.setState({
            scrolltop: scrollTop
        });

    }

    componentWillUnmount() {
        window.removeEventListener("scroll", this.handleScroll)
    }

    render() {
        const { height, scrolltop, action } = this.state;

        const CustomLink = styled(Link)(({ theme }) => ({
            textDecoration: 'none',
            color: "#55585D"
        }))
        const CustomAppBar = styled(AppBar)(({ theme }) => ({
            position: scrolltop >= height ? 'fixed' : 'absolute',
            background: '#fff',
            color: 'inherit',
            boxShadow: scrolltop >= height ? '0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);' : 'none'
        }))

        const CustomBox = styled(Box)(({ theme }) => ({
            display: 'flex',
            flexDirection: 'column',
            marginLeft: theme.spacing(3)
        }))

        let summary = action.summary + action.home;
        let about = summary + action.about;
        let education = about + action.education;
        let employement = education + action.employement;
        let skills = employement + action.skills;
        /*         console.log('scrollTop', scrolltop, scrolltop > height)
                console.log('action', action.home, height, scrolltop < action.home)
                console.log('action_summary', action.summary, height, scrolltop >= action.home)
                console.log('window', window.scrollY)
                console.log('windowpage', window.pageYOffset) */
        let action_ = (scrolltop >= height && scrolltop < action.home) ? "Home" :
            (scrolltop >= action.home && scrolltop < summary) ? (() => {
                return "Summary"
            })() :
                (scrolltop >= summary && scrolltop < about) ? 'About' :
                    (scrolltop >= about && scrolltop < education) ? 'Education' :
                        (scrolltop >= education && scrolltop < employement) ? 'Employement' :
                            (scrolltop >= employement && scrolltop < skills) ? 'Skills' :
                                (scrolltop === 0 ? '' : '')
        return (
            <Box sx={{ flexGrow: 1 }} >
                <CustomAppBar ref={divElement => (this.divElement = divElement)}>
                    <Toolbar>
                        <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
                            <IconButton
                                size="large"
                                edge="start"
                                color="inherit"
                                aria-label="open drawer"
                                sx={{ mr: 2 }}
                                onClick={this.props.props.openDrawer}
                            >
                                <MenuIcon />
                            </IconButton>
                        </Box>

                        <Box sx={{ display: { xs: 'none', sm: 'none', md: 'flex' } }}>
                            <CustomBox>
                                <CustomLink href="/" color="inherit" variant="h6">Portfolio</CustomLink>
                            </CustomBox>
                        </Box>
                        <Box sx={{ display: { xs: 'none', sm: 'none', md: 'flex' } }}>
                            <CustomBox>

                                <CustomLink href="#home" color="inherit" variant="body1" id="home-link" /* onClick={() => this.updateHash('#home')} */>Home</CustomLink>
                                {action_ === 'Home' ?
                                    (<div style={{ width: "100%", background: "#000", fontWeight: 700, height: '2px' }}></div>) :
                                    (<div></div>)}
                            </CustomBox>
                            <CustomBox>
                                <CustomLink href="#summary" color="inherit" variant="body1" id="summary-link"/* onClick={() => this.updateHash('#summary')} */>Summary</CustomLink>
                                {action_ === 'Summary' ?
                                    (<div style={{ width: "100%", background: "#000", fontWeight: 700, height: '2px' }}></div>) :
                                    (<div></div>)}
                            </CustomBox>
                            <CustomBox>
                                <CustomLink href="#about" color="inherit" variant="body1" id="about-link"/* onClick={() => this.updateHash('#about')} */>About</CustomLink>
                                {action_ === 'About' ?
                                    (<div style={{ width: "100%", background: "#000", fontWeight: 700, height: '2px' }}></div>) :
                                    (<div></div>)}
                            </CustomBox>
                            <CustomBox>
                                <CustomLink href="#education" color="inherit" variant="body1" id="education-link"/*  onClick={() => this.updateHash('#education')} */>Education</CustomLink>
                                {action_ === 'Education' ? (<div style={{ width: "100%", background: "#000", fontWeight: 700, height: '2px' }}></div>) : (<div></div>)}
                            </CustomBox>
                            <CustomBox>
                                <CustomLink href="#employement" color="inherit" variant="body1" id="employement-link"/*  onClick={() => this.updateHash('#employement')} */>Employement</CustomLink>
                                {action_ === "Employement" ? (<div style={{ width: "100%", background: "#000", fontWeight: 700, height: '2px' }}></div>) : (<div></div>)}
                            </CustomBox>
                            <CustomBox>
                                <CustomLink href="#skills" color="inherit" variant="body1" id="skills-link"/* onClick={() => this.updateHash('#skills')} */>Skills</CustomLink>
                                {action_ === "Skills" ? (<div style={{ width: "100%", background: "#000", fontWeight: 700, height: '2px' }}></div>) : (<div></div>)}
                            </CustomBox>
                            <div id="google_translate_element"></div>
                        </Box>

                        <Box sx={{ flexGrow: 1 }}>
                            <Box sx={{ display: { xs: 'flex', md: 'none' }, justifyContent: "center" }}>
                                {action_}
                            </Box>
                        </Box>

                        <Box sx={{ display: { xs: 'flex', md: 'flex' } }}>

                            <Button color='inherit' sx={{ textTransform: 'none', color: '#55585D' }} variant='body1'>

                                <DownloadIcon />
                                Resume
                            </Button>
                        </Box>
                    </Toolbar>
                </CustomAppBar>
            </Box>
        );
    }
}
