import React, { Component } from 'react'
import { useLocation, useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import About from './about'
import Education from './education'
import Employement from './employement'
import Header from './header'
import Home from './home'
import Skills from './skills'
import Summary from './summary'
import { drawerOpen, drawerClose} from '../redux/actionCreator'
import DrawerComponent from './drawer';
const mapStateToProps = state => {
    return {
        drawer: state.drawer
    };

}

const mapDispatchToProps = dispatch => ({
    openDrawer: () => dispatch(drawerOpen()),
    closeDrawer: () => dispatch(drawerClose())
})

function withRouter(Child) {
    return (props) => {
        const location = useLocation();
        const navigate = useNavigate();
        return <Child {...props} navigate={navigate} location={location} />;
    }
}
class Main extends Component {
    componentDidMount() {
        document.title = 'Swetabh Suman'
    }
    componentDidUpdate() {
        document.title = 'Swetabh Suman'
    }
    render() {
        return (
            <React.Fragment>
                <Header props={this.props} />
                <Home />
                <Summary />
                <About />
                <Education />
                <Employement />
                <Skills />
                <DrawerComponent props={this.props} />
            </React.Fragment>
        )
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));